import React, { useState } from 'react';

export default function Contact() {
    const [number, setNumber] = useState(false)
    const [email, setEmail] = useState(false)

const toggle = () => setNumber(prev => !prev);
const toggleEmail = () => setEmail(prev => !prev);

    return (
        <div style={{marginBottom: '35px', marginTop: '15px'}}>
     
                     <i onMouseEnter={toggle} onMouseLeave={toggle} style={{color: number ? 'red' : ''}} id='phone' class="fas fa-phone"></i>{' '}<a href="mailto:lexcpryor@gmail.com"><i onMouseEnter={toggleEmail} onMouseLeave={toggleEmail} style={{color: email ? 'blue' : ''}} id='email'class="fas fa-at"></i></a>
            <div><span id='phoneNumber' style={{float: 'center', display: number ? 'block' : 'none', }}>860-983-5858</span><span id='phoneNumber' style={{float: 'center', display: email ? 'block' : 'none', }}>lexcpryor@gmail.com</span></div>
        {' '}
            </div>
    )
}
